  src: url("../fonts/The-Seasons-Bold.woff2") format("woff2"), url("../fonts/The-Seasons-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.card {
  background: rgb(111 2 109 / 0.03);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgb(0 0 0 / 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgb(111 2 109 / 0.72);
}
.final-cta-card {
  background: rgb(14 12 14 / 0.35);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgb(0 0 0 / 0.1);
  backdrop-filter: blur(17.1px);
  -webkit-backdrop-filter: blur(17.1px);
  border: 1px solid rgb(111 2 109 / 0.72);
}
.header {
  background: rgb(200 0 212 / 0.03);
  box-shadow: 0 4px 30px rgb(0 0 0 / 0.1);
  backdrop-filter: blur(19px);
  -webkit-backdrop-filter: blur(19px);
  border: 0 0 1px 0 solid rgb(200 0 212 / 0.3);
}
.mesh-gradient-background-container {
  position: relative;
  min-height: 100svh;
}
.mesh-gradient-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: hsl(289 0% 3%);
  background-image: radial-gradient( circle at 52% 73%, hsl(289 0% 3%) 0, transparent 50% ), radial-gradient(circle at 0% 30%, hsl(310 100% 15%) 0, transparent 50%), radial-gradient(circle at 41% 26%, hsl(310 100% 15%) 0, transparent 50%), radial-gradient(circle at 41% 51%, hsl(310 100% 15%) 0, transparent 50%), radial-gradient(circle at 41% 88%, hsl(289 0% 3%) 0, transparent 50%), radial-gradient(circle at 76% 73%, hsl(289 0% 3%) 0, transparent 50%), radial-gradient(circle at 29% 37%, HSL(112 0% 3%) 0, transparent 50%);
  background-size: 350% 350%;
  filter: blur(1px);
  animation: moveBackground 15s linear infinite;
}
@keyframes moveBackground {
  0% {
    background-size: 100% 100%;
  }
  10% {
    background-size: 120% 100%;
  }
  20% {
    background-size: 190% 140%;
  }
  30% {
    background-size: 130% 110%;
  }
  40% {
    background-size: 150% 120%;
  }
  50% {
    background-size: 180% 160%;
  }
  60% {
    background-size: 220% 120%;
  }
  70% {
    background-size: 100% 100%;
  }
  80% {
    background-size: 120% 100%;
  }
  90% {
    background-size: 110% 240%;
  }
  100% {
    background-size: 100% 100%;
  }
}
.sr-only:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
.font-poppins {
  font-family: "Poppins", sans-serif;
}
.font-poppins-regular {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
.font-poppins-medium {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}
.font-poppins-bold {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.font-theseasons {
  font-family: "TheSeasons", serif;
  font-weight: 700;
}
body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
}
